/**
* CUSTOM SCROLLBAR
*/
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  border-radius: 8px; }

::-webkit-scrollbar-button {
  display: none; }

::-webkit-scrollbar-corner,
::-webkit-scrollbar-track {
  background: var(--scrollbar-bg, rgba(167, 167, 167, 0.2)); }

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb, #757575);
  border-radius: 8px; }

.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100vh;
  overflow: hidden;
  background-color: #3e4451;
  color: white; }
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 10vmin;
    pointer-events: none; }
  .App-header {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white; }
  .App-main {
    flex: 1;
    overflow: auto;
    padding: 0 1rem; }
  .App-footer {
    padding: 0.5rem; }
  .App-link {
    color: #61dafb; }
    .App-link:hover {
      color: #0596be; }
  .App-component {
    margin: 2rem auto 0 auto;
    max-width: 600px; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
